.preloader {
  /* */
  position: fixed;
  /*   */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /*    */
  background: #e0e0e0;
  /*        (    ,        ) */
  z-index: 1001;
}

.preloader__row {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: #005BAA;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}

.selectric-wrapper {
  position: relative;
  cursor: pointer;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: 1px solid #C2C6DC;
  border-radius: 40px;
  background: #FFFFFF;
  position: relative;
  overflow: hidden;
  min-width: 275px;
  background: #FFFFFF;
}
@media all and (max-width: 500px) {
  .selectric {
    min-width: auto;
  }
}
.selectric .label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 11px 35px 9px 24px;
  font-size: 1.2rem;
  line-height: 120%;
  letter-spacing: 0.075em;
  text-transform: uppercase;
  color: #232639;
  height: 35px;
  user-select: none;
}
@media all and (max-width: 500px) {
  .selectric .label {
    font-size: 18px;
  }
}
.selectric .button {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 35px;
  height: 35px;
  line-height: 35px;
  background-color: transparent !important;
  color: white;
  text-align: center;
  font: 0/0 a;
  *font: 20px/35px Lucida Sans Unicode, Arial Unicode MS, Arial;
  padding: 0 15px;
}
.selectric .button:after {
  content: " ";
  position: absolute;
  top: -6px;
  right: 4px;
  bottom: 0;
