.button-stroke {
  background: radial-gradient(90.03% 186.4% at 91.5% 97.89%, #00FFDB 0%, #00BBFF 100%);
  box-shadow: 0px 0.2083333333vw 0.9375vw rgba(0, 224, 255, 0);
