  position: relative;
  transition: 0.3s;
  cursor: pointer;
}
.button-stroke svg path {
  transition: 0.3s;
}
.button-stroke:before {
  position: absolute;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  left: 2px;
  top: 2px;
  content: "";
  display: block;
  background-color: white;
  border-radius: 2.5520833333vw;
  opacity: 1;
  transition: 0.3s;
}
